import Usa from "./policyLang/usa";
import Uk from "./policyLang/uk";

const policies = {
    usa: 'United States of America',
    uk: '–£–∫—Ä–∞—ó–Ω–∞',
  }
  
  let titles = document.querySelectorAll('.selected_policy');
  const showLists = document.querySelectorAll(".conteiner_policy");
  const forms = document.querySelectorAll('.policy');
  let isShowlist = false;
  let policy = getPolicy();

  document.addEventListener('DOMContentLoaded', () => {

      usa();
      
      titles.forEach(title => title.innerHTML = policy);
      const classRadio = getKey(policy);
      chengeContent(classRadio);
      hiddePolicyRadio(classRadio);
  });
  
  forms.forEach((form) => form.addEventListener('click', (e) => {
    if ([...e.target.classList].includes("policy_item")) {
      const currPolicy = e.target.children[0].value;
      hiddePolicyRadio(currPolicy);
      chengeContent(currPolicy);
      setPolicy(policies[currPolicy]);
      titles.forEach(title => {
        title.innerHTML = policies[currPolicy]; 
      });
    } 
  }));
  
  document.addEventListener('click', (e) => {

    if(isShowlist) {
      showLists.forEach(showList => {
        showList.classList.remove("show");
        showList.classList.add("hidden");
      });
      isShowlist = false;
    }else if ([...e.target.classList].includes("selected_policy")) {
      showLists.forEach(showList => {
        showList.classList.remove("hidden");
        showList.classList.add("show");
      });
      isShowlist = true;
    } 
  });
  
  function hiddePolicyRadio(policy) {
    let allRadios = document.querySelectorAll(".policy_item_input");
    allRadios.forEach(radio => {
      if([...radio.classList].includes(policy)) {
        radio.parentElement.style.display = "none";
        radio.checked = true;
      }else {
        radio.parentElement.style.display = "inherit";
       
      }
    })
  
  }
  
  function getPolicy () {
    let selected_policy = window.localStorage.getItem("policy");
    if(selected_policy !== "undefined") {
      return JSON.parse(selected_policy);
    }
    selected_policy = policies.usa;
    setPolicy(selected_policy);
    return selected_policy;
  }
  
  function setPolicy (policy) {
    window.localStorage.setItem("policy", JSON.stringify(policy));
  }

  const getKey = (policy) => {
    for(let key in policies) {
        if(policies[key] === policy){
            return key;
        }
    } 
}

function chengeContent(lang) {
  let content = {};
  switch(lang) {
    case "uk":
      content = Uk;
      break;
      default:
        content = Usa;
  }

  for(let key in content) {
    let elements = document.querySelectorAll("." + key);
    elements.forEach(element => element.innerHTML = content[key]);
  }
}

function usa() {
  let width = document.documentElement.clientWidth;
    if(width < 510) {
      const longName = document.querySelector(".hidden_xs");
      if(longName)longName.remove();
      policies.usa = 'USA';
    
    }else {
      const shortName = document.querySelector(".hidden_sm");
      if(shortName)shortName.remove();
    }
}