
document.querySelectorAll("[href*='#']")
.forEach(el => el.addEventListener('click', (e) => {
    e.preventDefault();
    const targetId = e.target.hash;
    const targetPositon = document.querySelector(targetId).offsetTop;
    const startPosition = window.pageYOffset;
    const distance = targetPositon - startPosition;
    const duration = 1000;
    let start = null;
    window.requestAnimationFrame(step);

    function step(timestamp) {
        if (!start) start = timestamp;
        const progress = timestamp - start;
        window.scrollTo(0, easeInOutCubic(progress, startPosition, distance, duration))
        if (progress < duration) window.requestAnimationFrame(step);
        
      }


}));
 
function easeInOutCubic(t, b, c, d) {
	t /= d/2;
	if (t < 1) return c/2*t*t*t + b;
	t -= 2;
	return c/2*(t*t*t + 2) + b;
};



function showFeatchest() {
  const featchest = document.querySelector(".featches");

  document.addEventListener('scroll', (e) => {
    const showLists = document.querySelectorAll(".conteiner_langs");
    showLists.forEach(showList => {
      if([...showList.classList].includes("show")) {
        showList.classList.remove("show");
        showList.classList.add("hidden");
      }
    });
    if(featchest) {
      if(pageYOffset < getTopCord(featchest)) {
        const list = [...featchest.children]
       list.forEach((el) => el.classList.add("featchest_animation"));
      }
    }
  })
}
showFeatchest();

function getTopCord(elem) { 
  var box = elem.getBoundingClientRect();
  return  box.top + pageYOffset;
  
}